<template>
  <v-form>
    <v-chip style="margin-left: 46vw; font-size: 17px;margin-top: 25vh">Register page</v-chip>
    <v-container style="margin-left: 25vw;width:50vw;margin-top:10vh">
      <v-row>
        <v-col
          cols="12"
          md="6"
        >
          <v-text-field
            v-model="firstName"
            :counter="15"
            label="Login"
            required
          ></v-text-field>
        </v-col>

        
        <v-col
          cols="12"
          md="6"
        >
          <v-text-field
            v-model="password"
            type="password"
            label="Password"
            required
          ></v-text-field>
        </v-col>
      </v-row>
    </v-container>



    <v-btn style="margin-left:45vw;width: 10vw;margin-top:5vh"
      elevation="2" @click="registerBrn"
    >Register</v-btn>


    <a href="/login" style="text-decoration:none;">
    <v-btn style="margin-left:43.5vw;width: 13vw;margin-top:5vh"
      elevation="2"
    >Go to login page</v-btn>
    </a>


    <a href="/" style="text-decoration:none;">
    <v-btn style="margin-left:43.5vw;width: 13vw;margin-top:5vh"
      elevation="2"
    >Go to main page</v-btn>
    </a>



  </v-form>
</template>

<script>
import router from "../router/index.js"
  export default {
    name: 'Login',
    data(){
      return {
        firstName: '',
        password: ''
      }
    },
    methods:{
      registerBrn(){
      if(!this.firstName.includes("script") && !this.firstName.includes("<") && !this.firstName.includes("&") && !this.password.includes("script") && !this.password.includes("<") && !this.password.includes("&") ){
      //let b = 0;
      if(this.firstName && this.password){
        const axios = require("axios")
        axios.put("http://localhost:8083/user/"+this.firstName+"/"+this.password)
        .then(function (response) {
            console.log(response)
                //b = response.data[0];
            })
            .catch(function (error) {
                alert(error);
            }).then(function() {
                alert("You have just registered")
                router.push({name: 'Login'})
            })
            



        
      }
      else{
        alert("Please fill the form!")
      }
      }
      else{
        alert("I know who you are")
      }
    }
    },
    components: {
    },
  }
</script>
