<script>
import Header from "@/components/Header"
  export default {
    data(){
      return{
        items: [],
        itemsFav: [],
        countLike: 0,
        countFavorite: 0,
        queueFavorites: [],
        queueLiked: [],
        items1: ['Year', 'Popularity'],
        title: localStorage.getItem("title"),
        author: localStorage.getItem("author"),
        sort: localStorage.getItem("sort"),


      }
    },
    components:{
      Header
    },
    methods:{
      search(){
        localStorage.setItem("title", this.title);
        localStorage.setItem("author", this.author);
        localStorage.setItem("sort", this.sort);
        location.reload()
      },
      logout(){
            localStorage.removeItem("user")
            localStorage.removeItem("userFirstName")
            localStorage.removeItem("userSecondName")
            localStorage.removeItem("userJob")
            this.$router.push({ path: '/login' })
        }
    },
    computed:{
      tabA(){
        return this.$store.state.tab
      },
       getFirstName(){
         return localStorage.getItem("userFirstName");
       },
       getLastName(){
         return localStorage.getItem("userSecondName");
       },
       getJob(){
         return localStorage.getItem("userJob");
       },
       getId(){
         return localStorage.getItem("user");
       },
       getBrowserId(){
         return localStorage.getItem("browser");
       }
      },
    created(){
      const axios = require("axios");
      const self = this

      
      if(!self.sort){
        axios.get("http://localhost:8083/articles/"+self.title+"/"+self.author)
        .then(function (response) {
          self.items = response.data
        })
        .catch(function (error) {
          console.log(error);
        })
      }
      else{
        if(self.sort.length == 4){
          axios.get("http://localhost:8083/articlesWith/"+self.title+"/"+self.author)
          .then(function (response) {
            self.items = response.data
          })
          .catch(function (error) {
            console.log(error);
          })
        }
        else{
          axios.get("http://localhost:8083/articlesWithPop/"+self.title+"/"+self.author)
          .then(function (response) {
            self.items = response.data
          })
          .catch(function (error) {
            console.log(error);
          })
        }
      }
      axios.get("http://localhost:8083/favorites")
      .then(function (response) {
        self.itemsFav = response.data
      })
      .catch(function (error) {
        console.log(error);
      })




      axios.get("http://localhost:8083/liked/"+self.getBrowserId)
      .then(function (response) {
        self.countLike = response.data[0].c
      })
      .catch(function (error) {
        console.log(error);
      })

      axios.get("http://localhost:8083/likeds/"+self.getBrowserId)
        .then(function (response) {
          self.queueLiked = response.data
        })
        .catch(function (error) {
          console.log(error);
        })

      if(self.getId){
        axios.get("http://localhost:8083/favorite/"+self.getId)
        .then(function (response) {
          self.countFavorite = response.data[0].c
        })
        .catch(function (error) {
          console.log(error);
        })

        axios.get("http://localhost:8083/favorites/"+self.getId)
        .then(function (response) {
          self.queueFavorites = response.data
        })
        .catch(function (error) {
          console.log(error);
        })
      }




      if(localStorage.getItem("browser") == null){
        axios.put('http://localhost:8083/browser/a')
        .then(function (response) {
            console.log(response);
        })
        .catch(function (error) {
            console.log(error);
        })
        .then(function(){
            axios.get('http://localhost:8083/browser')
            .then(function (response) {
                localStorage.setItem("browser", response.data[0].max)
            })
        })
      }
      else{
        console.log("You're here!")
      }





    }
  }
</script>

