<template>
    <div>
        <Header/>
    

    <v-main class="grey lighten-3">
        
      <v-container>
        <v-row>
          <v-col cols="3">
            <v-sheet rounded="lg">
            <div class="book_queue">
            <h1>Statistics</h1>
            <div class="book_queue_1">
                <span>{{ countLike }} likes</span>
            </div>

            <div class="book_queue_2">
                <v-btn
                    color="primary"
                    @click="like"
                    v-if="checkLike == 0"
                >
                    Like
                </v-btn>

                <v-btn
                    disabled
                    v-if="checkLike > 0"
                >
                    Liked
                </v-btn>
            </div>

            <br>
            <div class="book_queue_1" v-if="getId">
                <span >{{ countFav }} favorites</span>
            </div>

            <div class="book_queue_2" v-if="getId">
                <v-btn
                    color="primary"
                    @click="favorite"
                    v-if="checkFav == 0 && getId"
                >
                    Favorite
                </v-btn>

                <v-btn
                    disabled
                    v-if="checkFav > 0 && getId"
                >
                    Favorite
                </v-btn>
            </div>
            
        

        </div>
            </v-sheet>
          </v-col>

          <v-col cols="9">
