                <div class="home_special_offer">
            <div class="home_special_offer_1">
                <img src="data:image/jpeg;base64," alt="image_4">
            </div>

            <div class="home_special_offer_2">
                <strong>{{ info.title }}</strong>
                <h3>Publication: {{ info.publication }}</h3>
                <h3>Author: {{ info.author }}  </h3>
                <h3>Year: {{ info.year }}</h3>
                <p>{{ info.content }}</p>
            </div>
        </div>



            </v-sheet>
