<script>
import Header from "@/components/Header"

export default {
    name: 'Periodical',
    data(){
        return{
            info: [],
            countLike: 0,
            countFav: 0,
            checkLike: 0,
            checkFav: 0,
        }
    },
    components:{
        Header
    },
    computed:{
        getFirstName(){
      return localStorage.getItem("userFirstName")
    },
    getLastName(){
      return localStorage.getItem("userSecondName")
    },
    getJob(){
      return localStorage.getItem("userJob")
    },
    getId(){
      return localStorage.getItem("user")
    },
    getBrowser(){
        return localStorage.getItem("browser")
    }
    },
    methods: {
        like(){
            const axios = require("axios")
            axios.post("http://localhost:8083/like/"+this.$route.params.id,{
                browser_id: this.getBrowser,
                article_id: this.$route.params.id,
                article_title: this.info.title
            })
            .then(function(response){
                console.log(response)
            })
            .then(function(error){
                console.log(error)
            })
            
            location.reload();
        },

        favorite(){
            const axios = require("axios")
            axios.post("http://localhost:8083/favorite/"+this.$route.params.id,{
                user_id: this.getId,
                article_id: this.$route.params.id,
                article_title: this.info.title
            })
            .then(function(response){
                console.log(response)
            })
            .then(function(error){
                console.log(error)
            })
            
            location.reload();
        },

    },
    created(){
        const axios = require("axios")
        const self = this //this.$route.params.id

        axios.get("http://localhost:8083/article/"+this.$route.params.id)
        .then(function(response){
            self.info = response.data[0]
        })
        .then(function(error){
            console.log(error)
        })


        axios.get("http://localhost:8083/like/"+this.$route.params.id)
        .then(function(response){
            self.countLike = response.data[0].c
        })
        .then(function(error){
            console.log(error)
        })

        axios.get("http://localhost:8083/favoriteC/"+this.$route.params.id)
        .then(function(response){
            self.countFav = response.data[0].c
        })
        .then(function(error){
            console.log(error)
        })

    

        axios.get("http://localhost:8083/like/"+this.$route.params.id+"/"+self.getBrowser)
        .then(function(response){
            self.checkLike = response.data[0].c
        })
        .then(function(error){
            console.log(error)
        })


        axios.get("http://localhost:8083/favorite/"+this.$route.params.id+"/"+self.getId)
        .then(function(response){
            self.checkFav = response.data[0].c
        })
        .then(function(error){
            console.log(error)
        })

        





        
    }
}
</script>

